.wpe-like-button__button {
	align-items: center;
	appearance: none;
	background: none;
	border: none;
	cursor: pointer;
	display: flex;
	padding: 0;
}

.wpe-like-button__button:not(.wpe-liked) {
	fill: currentColor;
	stroke-width: 0;
}

.wp-block-wpe-like-button .empty-heart_svg__wpe-icon--heart {
	height: auto;
	width: 18px;
}